import React, { forwardRef, useEffect, useRef } from "react";

import Button from "../Button/Button";
import IconButton from "../Button/IconButton";
import Typography from "../Typography/Typography";

import { FilterPageProps } from "./FilterMenu.types";
import {
	FilterContentDiv,
	FilterPageButton,
	FilterPageContent,
	FilterPageHeader,
	FilterPageTitle,
} from "./FilterPage.styles";

const FilterPage = forwardRef<HTMLDivElement, FilterPageProps>(({
	dataQA,
	filterId,
	filterName,
	filterComponent,
	addButtonFunc = () => { },
	closeButtonFunc,
	$menuWidth,
	$menuHeight,
	addButtonText = "Add",
}, ref) => {
	const buttonRef = useRef<HTMLButtonElement>(null);
	useEffect(() => {
		if (buttonRef) {
			buttonRef.current?.focus();
		}
	}, []);

	const handleClick = () => addButtonFunc(filterName, filterId, filterComponent);

	return (
		<FilterPageContent
			data-qa="Filter Section"
			ref={ref}
			$menuWidth={$menuWidth}
			$menuHeight={$menuHeight}
		>
			<FilterPageHeader $menuWidth={$menuWidth}>
				<IconButton
					dataQA={`${dataQA}-close-filter-button`}
					aria-label="Close Filter Content"
					size="Small"
					variant="tertiary"
					icon="ChevronLeft"
					onClick={closeButtonFunc}
					ref={buttonRef}
				/>
				<FilterPageTitle>
					<Typography variant="bodyCopyBold" dataQA={filterName}>
						{filterName}
					</Typography>
				</FilterPageTitle>
			</FilterPageHeader>
			<FilterContentDiv>
				{filterComponent}
			</FilterContentDiv>
			<FilterPageButton $menuWidth={$menuWidth}>
				<Button
					dataQA={`${dataQA}-add-filter-button`}
					aria-label="add filter"
					buttonText={addButtonText}
					variant="primary"
					size="Medium"
					onClick={() => handleClick()}
				/>
			</FilterPageButton>
		</FilterPageContent>

	);
});

FilterPage.displayName = "FilterPage";

export default FilterPage;

